'use client';

import React, { useMemo, useState } from 'react';
import { BookOpen, Globe, ArrowRight, Search, Hash } from 'lucide-react';
// Menggunakan data JSON utama agar sinkron dengan detail page
import treatyDataRaw from '@/data/treaties.json';
import Link from 'next/link';

interface Treaty {
  pair: string;
}

export default function EncyclopediaPage() {
  const [searchTerm, setSearchTerm] = useState('');
  
  // Casting data untuk kepatuhan TypeScript 5.9
  const treatyData = (treatyDataRaw as unknown) as Treaty[];

  /**
   * HELPER: createSlug
   * Wajib identik dengan [slug]/page.tsx untuk mencegah 404
   */
  const createSlug = (pair: string) => {
    if (!pair) return "";
    const parts = pair.split(/[-–—]/); 
    if (parts.length < 2) return "";
    const source = parts[0].trim().toLowerCase().replace(/\s+/g, '-');
    const target = parts[1].trim().toLowerCase().replace(/\s+/g, '-');
    return `/tax-treaty/tax-treaty-${source}-to-${target}`;
  };

  // 1. PERBAIKAN FATAL: Menambahkan pengecekan t.pair agar tidak crash
  const groupedTreaties = useMemo(() => {
    const filtered = treatyData.filter(t => 
      t && t.pair && t.pair.toLowerCase().includes(searchTerm.toLowerCase())
    );

    const groups: { [key: string]: Treaty[] } = {};
    filtered.forEach(t => {
      // Safety check tambahan sebelum mengambil huruf pertama
      const firstLetter = t.pair.trim()[0]?.toUpperCase() || '#';
      if (!groups[firstLetter]) groups[firstLetter] = [];
      groups[firstLetter].push(t);
    });

    return Object.keys(groups).sort().reduce((acc, key) => {
      acc[key] = groups[key];
      return acc;
    }, {} as { [key: string]: Treaty[] });
  }, [searchTerm, treatyData]);

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* HERO & SEARCH SECTION */}
      <section className="max-w-6xl mx-auto pt-24 pb-16 px-8 text-center space-y-8">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <BookOpen className="w-4 h-4" /> Global Registry Hub
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          Tax Treaty <span className="text-[#D4AF37]">Index.</span>
        </h1>
        
        <div className="max-w-xl mx-auto relative group">
          <Search className="absolute left-6 top-1/2 -translate-y-1/2 text-gray-300 group-focus-within:text-[#D4AF37] transition-colors" size={20} />
          <input 
            type="text"
            placeholder="Search jurisdiction (e.g. United States)..."
            className="w-full bg-white border border-gray-100 py-6 pl-16 pr-8 rounded-3xl shadow-sm focus:ring-2 focus:ring-[#D4AF37] outline-none transition-all text-[16px]"
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </section>

      {/* ALPHABETICAL DIRECTORY */}
      <section className="max-w-7xl mx-auto px-8 space-y-20">
        {Object.entries(groupedTreaties).map(([letter, items]) => (
          <div key={letter} className="space-y-8">
            <div className="flex items-center gap-6">
              <div className="w-16 h-16 bg-black text-[#D4AF37] rounded-2xl flex items-center justify-center text-3xl font-serif italic shadow-xl">
                {letter}
              </div>
              <div className="h-[1px] flex-1 bg-gray-100"></div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {items.map((item, idx) => (
                <Link 
                  key={idx} 
                  href={createSlug(item.pair)}
                  className="flex items-center justify-between p-6 bg-white border border-gray-50 rounded-2xl hover:border-[#D4AF37] hover:shadow-lg transition-all group"
                >
                  <div className="flex items-center gap-4">
                    <Globe size={18} className="text-gray-200 group-hover:text-[#D4AF37]" />
                    <span className="text-[15px] font-medium text-gray-600 group-hover:text-black">
                      {item.pair.replace('-', ' → ')}
                    </span>
                  </div>
                  <ArrowRight size={14} className="text-gray-200 group-hover:text-black group-hover:translate-x-1 transition-all" />
                </Link>
              ))}
            </div>
          </div>
        ))}
      </section>

      {/* FOOTER SECTION */}
      <section className="max-w-4xl mx-auto mt-32 px-8 py-20 bg-black rounded-[4rem] text-center space-y-8 shadow-2xl">
        <Hash className="mx-auto text-[#D4AF37] opacity-50" size={40} />
        <h3 className="text-2xl font-serif italic text-white">Global Compliance Directory</h3>
        <p className="text-gray-400 font-light leading-relaxed italic text-[15px]">
          Our jurisdictional encyclopedia indexes 10,000+ bilateral tax corridors. 
          Each entry provides a 2026 technical analysis of withholding tax (WHT) instruments.
        </p>
      </section>
    </div>
  );
}